/**
 * Input Switch 
 */
.switch-currency {
  z-index: 2;
  position: absolute;
  top: -2rem;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.8rem;
  color: black;
}
.switch-currency img {
  max-width: 2rem;
  -o-object-fit: cover;
     object-fit: cover;
}
.switch-currency .spanish {
  margin-right: 0.5rem;
}
.switch-currency .british {
  margin-left: 0.5rem;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: whitesmoke;
  transition: 0.4s;
}

.slider-simple {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(192, 192, 192);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #296eb4;
  transition: 0.4s;
}

.slider-simple:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #296eb4;
  transition: 0.4s;
}

input:checked + .slider,
input:checked + .slider-simple {
  background-color: whitesmoke;
}

input:focus + .slider,
input:focus + .slider-simple {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before,
input:checked + .slider-simple:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round,
.slider-simple.round {
  border-radius: 34px;
}

.slider.round:before,
.slider-simple.round:before {
  border-radius: 50%;
}

@media (max-width: 1400px) {
  .switch-currency {
    z-index: 2;
    position: static;
    top: unset;
    left: unset;
    transform: unset;
  }
  .switch-currency img {
    max-width: 1.6rem;
  }
  .switch-currency .spanish {
    margin-right: 0.5rem;
  }
  .switch-currency .british {
    margin-left: 0.5rem;
  }
  .switch-currency .switch {
    width: 50px;
    height: 24px;
  }
  .switch-currency .slider:before {
    height: 24px;
    width: 24px;
    left: 0;
    bottom: 0;
  }
  .switch-currency .slider-simple:before {
    height: 24px;
    width: 24px;
    left: 0;
    bottom: 0;
  }
  .switch-currency input:checked + .slider:before,
.switch-currency input:checked + .slider-simple:before {
    transform: translateX(26px);
  }
}
