.section,
.section-closed {
  position: absolute;
  min-height: 25rem;
  max-height: 25rem;
  min-width: 40rem;
  max-width: 40rem;
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  box-shadow: 1px 1px 1px 1px #333;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  overflow-y: auto;
  transition: all 0.5s ease-in;
  transition: all 0.5s ease-out;
  scrollbar-width: thin;
  scrollbar-color: #296eb4 whitesmoke;
}
.section::-webkit-scrollbar,
.section-closed::-webkit-scrollbar {
  width: 1rem;
}
.section::-webkit-scrollbar-track,
.section-closed::-webkit-scrollbar-track {
  border-radius: 0.2rem;
}
.section::-webkit-scrollbar-thumb,
.section-closed::-webkit-scrollbar-thumb {
  background-color: #296eb4;
  border-radius: 6px;
  border: 1px solid whitesmoke;
}
.section .section__header,
.section-closed .section__header {
  border-bottom: double 2px transparent;
  background-image: linear-gradient(white, white), linear-gradient(to right, #296eb4, #333);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
.section .section__header .section__header__content,
.section-closed .section__header .section__header__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.section .section__content,
.section-closed .section__content {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.section .section__content.--centered,
.section-closed .section__content.--centered {
  justify-content: center;
  margin: auto 0;
}
.section .section__item,
.section-closed .section__item {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  border-bottom: double 2px transparent;
  background-image: linear-gradient(white, white), linear-gradient(to right, #296eb4, #333);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
.section .section__item:first-child,
.section-closed .section__item:first-child {
  margin-top: 0;
}
.section .text-list,
.section-closed .text-list {
  display: flex;
  flex-direction: row;
}
.section .text-list .text,
.section .text-list .list,
.section-closed .text-list .text,
.section-closed .text-list .list {
  width: 50%;
}
.section .text-list .text,
.section-closed .text-list .text {
  font-size: 1.2rem;
  font-weight: bold;
  color: rgb(124, 124, 124);
}
.section .text-list .list,
.section-closed .text-list .list {
  display: flex;
  flex-direction: column;
  padding-left: 2rem;
}
.section .text-list .list .item,
.section-closed .text-list .list .item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0.5rem 0;
  font-size: 1.2rem;
}
.section .text-list .list .item .label,
.section-closed .text-list .list .item .label {
  background-color: #296eb4;
  color: rgb(255, 255, 255);
  padding: 0 0.2rem;
  border-radius: 0.2rem;
  font-weight: bold;
}
.section .text-list .list .item .value,
.section-closed .text-list .list .item .value {
  font-weight: bold;
  color: rgb(124, 124, 124);
}
.section .icon-list,
.section-closed .icon-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.section .icon-list i,
.section-closed .icon-list i {
  color: rgb(255, 255, 255);
  background-color: #296eb4;
  padding: 0.7rem;
  border-radius: 50%;
  font-size: 1.5rem;
  margin-right: 1rem;
}
.section .icon-list i:last-child,
.section-closed .icon-list i:last-child {
  margin-right: 0;
}
.section .icon-list .fas.fa-laptop-code,
.section .icon-list .fas.fa-dumbbell,
.section-closed .icon-list .fas.fa-laptop-code,
.section-closed .icon-list .fas.fa-dumbbell {
  padding: 0.7rem 0.5rem;
}
.section .project-list,
.section-closed .project-list {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: baseline;
}
.section .contact-list,
.section-closed .contact-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 20rem;
  align-self: center;
}

.section-closed {
  min-width: 1rem;
  max-width: 1rem;
  opacity: 0;
  overflow: hidden;
  transition: all 0.5s ease-in;
}

.section__item-p {
  display: flex;
  flex-direction: row;
  margin-bottom: 1rem;
  font-weight: bold;
}

.section__item-p a {
  margin-left: 0.3rem;
}

@media (max-width: 1400px) {
  .section,
.section-closed {
    position: static;
    min-height: 100%;
    max-height: 200%;
    min-width: 40rem;
    max-width: 40rem;
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    box-shadow: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
    overflow-y: auto;
    transition: all 1s ease-in;
    transition: all 1s ease-out;
  }
  .section-closed {
    min-width: unset;
    max-width: unset;
    min-height: 0.01rem;
    max-height: 0.01rem;
    opacity: 0;
    overflow: hidden;
    transition: all 1s ease-in;
    transition: all 1s ease-out;
  }
  .section__header {
    border-bottom: double 2px transparent;
    background-image: linear-gradient(white, white), linear-gradient(to right, #296eb4, #333);
    background-origin: border-box;
    background-clip: content-box, border-box;
  }
  .section__header .section__header__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
  }
}
@media (max-width: 800px) {
  .section,
.section-closed {
    min-width: 100%;
    max-width: unset;
    width: 100%;
  }
  .section-closed {
    min-width: unset;
    max-width: unset;
    min-height: 0.01rem;
    max-height: 0.01rem;
    opacity: 0;
    overflow: hidden;
    transition: all 1s ease-in;
    transition: all 1s ease-out;
  }
}
@media (max-width: 640px) {
  .section .section__content,
.section-closed .section__content {
    padding: 3% 5% 5% 5%;
  }
  .section .text-list,
.section-closed .text-list {
    display: flex;
    flex-direction: column;
  }
  .section .text-list .text,
.section .text-list .list,
.section-closed .text-list .text,
.section-closed .text-list .list {
    text-align: justify;
    width: 100%;
  }
  .section .text-list .list,
.section-closed .text-list .list {
    padding-top: 0.5rem;
    padding-left: 0;
  }
}
