/** Hide or Show Elements */
.hide {
  display: none;
}

.hide-sm {
  display: unset;
}

.show-sm {
  display: none;
}

.show-landscape {
  display: none;
}

.hide-landscape {
  display: inline;
}

.show-portrait {
  display: none;
}

.hide-portrait {
  display: inline;
}

/*****************************************************************************************************
* Display Mobile
*****************************************************************************************************/
@media (max-width: 700px) {
  /** Hide or Show Elements */
  .hide-sm {
    display: none;
  }
  .show-sm {
    display: unset;
  }
}
@media (max-width: 700px) and (max-width: 45rem) {
  .show-landscape {
    display: inline;
  }
}
@media (max-width: 700px) and (max-width: 22.5rem) {
  .show-landscape {
    display: none;
  }
}
@media (max-width: 700px) and (max-width: 45rem) {
  .hide-landscape {
    display: none;
  }
}
@media (max-width: 700px) and (max-width: 22.5rem) {
  .hide-landscape {
    display: inline;
  }
}
@media (max-width: 700px) and (max-width: 45rem) {
  .show-portrait {
    display: none;
  }
}
@media (max-width: 700px) and (max-width: 22.5rem) {
  .show-portrait {
    display: inline;
  }
}
@media (max-width: 700px) and (max-width: 45rem) {
  .hide-portrait {
    display: inline;
  }
}
@media (max-width: 700px) and (max-width: 22.5rem) {
  .hide-portrait {
